
import { axiosGet } from '../request'

/*  */
export const getClaimApply = (params) =>
  axiosGet({
    config: { url: '/member/claim/getProductClaims', params }
  })

/*  */
export const claimApplySubmit = (params) =>
  axiosGet({
    config: { url: '/member/claim/applyCliam', params }
  })

/*  */
export const getClaimList = (params) =>
  axiosGet({
    config: { url: '/member/claim/getClaimList', params }
  })

/*  */
export const claimRevoke = (params) =>
  axiosGet({
    config: { url: '/member/claim/cancel', params }
  })

/*  */
export const claimView = (params) =>
  axiosGet({
    config: { url: '/member/claim/view', params }
  })
