export default Vue => {
  Vue.filter('claimStatus', function (value) {
    let res = ''
    switch (parseInt(value)) {
      case 0 : res = ''; break
      case 1 : res = ''; break
      case 2 : res = ''; break
      case 3 : res = ''; break
      case 4 : res = ''; break
      case 5 : res = ''; break
      case 6 : res = ''; break
      default: console.error('not defined status: ' + value)
    };
    return res
  })
  Vue.filter('recordStatus', function (value) {
    let res = ''
    switch (value) {
      case 'upload' : res = ''; break
      case 'uploaded' : res = ''; break
      case 'check' : res = ''; break
      case 'unpassed' : res = ''; break
      case 'passed' : res = ''; break
      default: console.error('not defined record-status: ' + value)
    };
    return res
  })
}
