import { axiosGet, axiosPost } from '../request'

export const getGroupInsuranceList = (params) =>
  axiosGet({
    config: { url: '/member/groupInsurance/list', params }
  })

export const getGroupInsuranceDetail = (params) =>
  axiosGet({
    config: { url: '/member/groupInsurance/detail', params }
  })

